.skillItem {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    border: 1px solid #191954;
    height: 250px;
    width: 150px;
    background-color: rgba(128, 128, 234, 0.77);
}

.skillIcon {
    height: 70px;
    width: 70px;
}

.description {
    background-color: rgba(135, 135, 155, 0.77);
    width: 100%;
    height: 40%;
    border: 1px solid;
}